import React from "react";
import { FaTelegram } from "react-icons/fa";
import { RiKakaoTalkFill } from "react-icons/ri";
import {
  Services2Container,
  Services2H1,
  Services2Wrapper,
  Services2Card,
  Services2Icon,
  Services2H2,
  Services2Link,
} from "./Services2Elements";

const Services2 = () => {
  return (
    <Services2Container id="channel">
      <Services2H1>Contact us</Services2H1>
      <Services2Wrapper>
        <Services2Link
          href="https://open.kakao.com/o/ghlLJBSc"
          target="_blank"
          aria-label="KakaoTalk"
        >
          <Services2Card>
            <Services2Icon>
              <RiKakaoTalkFill size="80" color="#00b992" />
            </Services2Icon>
            <Services2H2>TERA 오픈톡</Services2H2>
          </Services2Card>
        </Services2Link>
        <Services2Link
          href="https://t.me/tera_cardano"
          target="_blank"
          aria-label="Telegram"
        >
          <Services2Card>
            <Services2Icon>
              <FaTelegram size="80" color="#00b992" />
            </Services2Icon>
            <Services2H2>TERA Telegram</Services2H2>
          </Services2Card>
        </Services2Link>
      </Services2Wrapper>
    </Services2Container>
  );
};

export default Services2;
