import React, { useEffect, useState } from "react";
import {
  ServicesContainer,
  ServicesH1,
  ServicesWrapper,
  ServicesCard,
  ServicesH2,
  ServicesP,
  ServicesP2,
  ServicesLink,
} from "./ServicesElements";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";

const Services = () => {
  const [totalData, setTotalData] = useState("");
  const [totalData2, setTotalData2] = useState("");

  const callApi1 = async () => {
    const response = await fetch(
      "https://js.adapools.org/pools/7113de447fb62c38e3e3aeec434cac6fc76e05e997b84227e9c637b8/summary.json"
    );
    const body = await response.json();

    return body;
  };

  const callApi2 = async () => {
    const response = await fetch(
      "https://js.adapools.org/pools/50e113d1ada46a83a16d5ee6cfdc1a8baed6634c9f046ed30131eb11/summary.json"
    );
    const body = await response.json();

    return body;
  };

  useEffect(() => {
    callApi1()
      .then((res) => setTotalData(res.data))
      .catch((err) => console.log(err));

    callApi2()
      .then((res) => setTotalData2(res.data))
      .catch((err) => console.log(err));
  }, []);

  return (
    <ServicesContainer id="services">
      <ServicesH1>Our Pools</ServicesH1>
      <ServicesWrapper>
        <ServicesLink
          href="https://pooltool.io/pool/7113de447fb62c38e3e3aeec434cac6fc76e05e997b84227e9c637b8/epochs"
          target="_blank"
          aria-label="TERA1"
        >
          <ServicesCard>
            <ServicesH2>⭐TERA #1⭐</ServicesH2>
            <Table>
              <TableBody>
                <TableRow>
                  <TableCell>
                    <ServicesP>Pool ID</ServicesP>
                  </TableCell>
                  <TableCell>
                    <ServicesP>7113de447fb62c...</ServicesP>
                  </TableCell>
                </TableRow>
                <TableRow>
                  <TableCell>
                    <ServicesP>Delegators</ServicesP>
                  </TableCell>
                  <TableCell>
                    <ServicesP>{totalData.delegators}</ServicesP>
                  </TableCell>
                </TableRow>
                <TableRow>
                  <TableCell>
                    <ServicesP>Live Stake</ServicesP>
                  </TableCell>
                  <TableCell>
                    <ServicesP>
                      {totalData.total_stake > 1000000000000
                        ? (totalData.total_stake / 1000000000000).toFixed(2) +
                          "M"
                        : (totalData.total_stake / 1000000000).toFixed(2) +
                          "K"}{" "}
                      {"(" + (totalData.saturated * 100).toFixed(1) + "%)"}
                    </ServicesP>
                  </TableCell>
                </TableRow>
                <TableRow>
                  <TableCell>
                    <ServicesP>Blocks Lifetime</ServicesP>
                  </TableCell>
                  <TableCell>
                    <ServicesP>
                      {String(
                        Number(totalData.blocks_epoch) +
                          Number(totalData.blocks_lifetime)
                      )}
                    </ServicesP>
                  </TableCell>
                </TableRow>
                <TableRow>
                  <TableCell>
                    <ServicesP>Fees</ServicesP>
                  </TableCell>
                  <TableCell>
                    <ServicesP2>1% + 340</ServicesP2>
                  </TableCell>
                </TableRow>
              </TableBody>
            </Table>
          </ServicesCard>
        </ServicesLink>
        <ServicesLink
          href="https://pooltool.io/pool/50e113d1ada46a83a16d5ee6cfdc1a8baed6634c9f046ed30131eb11/epochs"
          target="_blank"
          aria-label="TERA2"
        >
          <ServicesCard>
            <ServicesH2>⚡TERA #2⚡</ServicesH2>
            <Table>
              <TableBody>
                <TableRow>
                  <TableCell>
                    <ServicesP>Pool ID</ServicesP>
                  </TableCell>
                  <TableCell>
                    <ServicesP>50e113d1ada46a...</ServicesP>
                  </TableCell>
                </TableRow>
                <TableRow>
                  <TableCell>
                    <ServicesP>Delegators</ServicesP>
                  </TableCell>
                  <TableCell>
                    <ServicesP>{totalData2.delegators}</ServicesP>
                  </TableCell>
                </TableRow>
                <TableRow>
                  <TableCell>
                    <ServicesP>Live Stake</ServicesP>
                  </TableCell>
                  <TableCell>
                    <ServicesP>
                      {totalData2.total_stake > 1000000000000
                        ? (totalData2.total_stake / 1000000000000).toFixed(2) +
                          "M"
                        : (totalData2.total_stake / 1000000000).toFixed(2) +
                          "K"}{" "}
                      {"(" + (totalData2.saturated * 100).toFixed(1) + "%)"}
                    </ServicesP>
                  </TableCell>
                </TableRow>
                <TableRow>
                  <TableCell>
                    <ServicesP>Blocks Lifetime</ServicesP>
                  </TableCell>
                  <TableCell>
                    <ServicesP>
                      {String(
                        Number(totalData2.blocks_epoch) +
                          Number(totalData2.blocks_lifetime)
                      )}
                    </ServicesP>
                  </TableCell>
                </TableRow>
                <TableRow>
                  <TableCell>
                    <ServicesP>Fees</ServicesP>
                  </TableCell>
                  <TableCell>
                    <ServicesP2>0% + 340</ServicesP2>
                  </TableCell>
                </TableRow>
              </TableBody>
            </Table>
          </ServicesCard>
        </ServicesLink>
      </ServicesWrapper>
    </ServicesContainer>
  );
};

export default Services;
