import React from "react";
import { animateScroll as scroll } from "react-scroll";
import { FaTelegram, FaTwitter, FaYoutube } from "react-icons/fa";
import {
  FooterContainer,
  FooterWrap,
  SocialMedia,
  SocialMediaWrap,
  SocialLogo,
  WebsiteRights,
  SocialIcons,
  SocialIconLink,
} from "./FooterElements";
import { RiKakaoTalkFill } from "react-icons/ri";

function Footer() {
  const toggleHome = () => {
    scroll.scrollToTop();
  };
  return (
    <FooterContainer>
      <FooterWrap>
        <SocialMedia>
          <SocialMediaWrap>
            <SocialLogo to="/" onClick={toggleHome}>
              TERA
            </SocialLogo>
            <WebsiteRights>
              TERA © {new Date().getFullYear()} All rights reserved.
            </WebsiteRights>
            <SocialIcons>
              <SocialIconLink
                href="https://open.kakao.com/o/ghlLJBSc"
                target="_blank"
                aria-label="KakaoTalk"
              >
                <RiKakaoTalkFill />
              </SocialIconLink>
              <SocialIconLink
                href="https://t.me/tera_cardano"
                target="_blank"
                aria-label="Telegram"
              >
                <FaTelegram />
              </SocialIconLink>
              <SocialIconLink aria-label="Youtube">
                <FaYoutube />
              </SocialIconLink>
              <SocialIconLink aria-label="Twitter">
                <FaTwitter />
              </SocialIconLink>
            </SocialIcons>
          </SocialMediaWrap>
        </SocialMedia>
      </FooterWrap>
    </FooterContainer>
  );
}

export default Footer;
